    if (error) return <p style={{ color: 'red' }}>Ø®Ø·Ø§: {error}</p>;
    if (!shop) return <p>Ø§Ø·Ù„Ø§Ø¹Ø§Øª ÙØ±ÙˆØ´Ú¯Ø§Ù‡ ÛŒØ§ÙØª Ù†Ø´Ø¯.</p>;

